html[data-theme="light"] {
  --background-clr: #f6f6f6;
  --main-text-clr: #111;
  --header-clr: #fff;
  --scroll-bar-clr: #e0e0e0;
  --active-category-clr: #4f2ee8;
  --category-text-clr: rgba(17, 17, 17, .6);
  --shopping-list-clr: rgba(0, 0, 0, .6);
  --modal-bg: #fff;
}

html[data-theme="dark"] {
  --background-clr: #202024;
  --main-text-clr: #fff;
