  --shopping-list-clr: rgba(255, 255, 255, .6);
  --modal-bg: #202024;
}

body {
  color: var(--main-text-clr);
  background-color: var(--background-clr);
  font-family: DM Sans;
  font-style: normal;
  transition: background-color .1s linear;
}

.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.no-scroll {
  overflow: hidden;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

