@mixin font-auth {
  font-family: DM Sans;
  font-size: 18px;
  font-style: normal;
  font-weight: 700;
  // line-height: 24px; /* 133.333% */
  letter-spacing: -0.18px;
  text-transform: uppercase;
}

@mixin sign-up-btn {
  margin-top: 40px;
  margin-left: 20px;

  display: flex;
  align-items: center;
  padding: 14px;
  justify-content: space-between;
  gap: 9px;
  min-width: 184px;
  height: 48px;
  flex-shrink: 0;
  cursor: pointer;
  color: $color-white;
  font-family: DM Sans;
  font-size: 22px;
  font-style: normal;
  font-weight: 500;
  line-height: 18px; /* 128.571% */
  letter-spacing: -0.56px;

  border-radius: 18px;
  border: 1.5px solid $color-black;
  background: $color-violet;
}

@mixin center-flex {
  display: flex;
  justify-content: center;
  align-items: center;
  // justify-content: center;
}

@mixin mobile {
  @media only screen and (min-width: 375px) {
    @content;
  }
}

@mixin mobile-tablet {
  @media only screen and (min-width: 375px) and (max-width: 767px) {
    @content;
  }
}

@mixin tablet {
  @media only screen and (min-width: 768px) and (max-width: 1439px) {
    @content;
  }
}

@mixin tablet-desktop {
  @media only screen and (min-width: 768px) {
    @content;
  }
}

@mixin desktop {
  @media only screen and (min-width: 1440px) {
    @content;
  }
}
